.Dashboard {
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: 50px;
  margin-right: 200px;
  text-align: center;
}

.Dashboard .profile {
  float: right;
  margin-right: 800px;
  margin-top: 20px;
  letter-spacing: 0.5px;
  -webkit-box-shadow: 1px 1px 0 0 #999;
          box-shadow: 1px 1px 0 0 #999;
  display: inline-block !important;
  font-size: 16px;
  line-height: 1.5;
  border-radius: 4px;
  color: #000000;
  font-weight: 500;
  background-color: #fff;
  border-color: #3f99a1;
  text-align: center;
  vertical-align: middle;
  border-width: thin;
  border-style: solid;
}

.Dashboard .registration {
  float: left;
  position: relative;
  margin-left: 50px;
  margin-top: 20px;
  letter-spacing: 0.5px;
  -webkit-box-shadow: 1px 1px 0 0 #999;
          box-shadow: 1px 1px 0 0 #999;
  display: inline-block !important;
  font-size: 16px;
  line-height: 1.5;
  border-radius: 4px;
  color: #000000;
  font-weight: 500;
  background-color: #fff;
  border-color: #3f99a1;
  text-align: center;
  vertical-align: middle;
  border-width: thin;
  border-style: solid;
}

.Dashboard .abstract {
  float: left;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  margin-left: 50px;
  margin-top: 20px;
  letter-spacing: 0.5px;
  -webkit-box-shadow: 1px 1px 0 0 #999;
          box-shadow: 1px 1px 0 0 #999;
  display: inline-block !important;
  font-size: 16px;
  line-height: 1.5;
  border-radius: 4px;
  color: #000000;
  font-weight: 500;
  background-color: #fff;
  border-color: #3f99a1;
  text-align: center;
  vertical-align: middle;
  border-width: thin;
  border-style: solid;
}



